.search {
    display: flex;
    justify-content: right;
    background-color: #1e1e1e;
    align-items: right;
    font-size: large;
    overflow-x: auto;
    padding: 10px;
    height: 60px;
    width: 30%;
}

.search-input {
    border-radius: 6px;
    padding: 10px;
    margin: 0 5px;
    cursor: pointer;
}